.item {
  display: flex;
  align-items: center;
  width: 270px;
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.item:last-child {
  margin-bottom: 0px;
}

.status {
  display: block;
  width: 20px;
  height: 20px;
  margin-right: 20px;
  border-radius: 50%;
}

.avatar {
  margin-right: 20px;
}

.name {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.63;
  color: #2a363b;
}
