.profile {
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 340px;
  padding: 30px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.4);
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
}

.name {
  margin-bottom: 20px;
  font-size: 26px;
  font-weight: 700;
  text-align: center;
}

.tag {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  color: #bdbdbd;
}

.location {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
}

.avatar {
  margin-bottom: 20px;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  object-fit: cover;
  background-color: #fff;
  border: 1px solid #bdbdbd;
}

.stats {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  border-radius: 5px;
}

.stats-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 10px;
  border-right: 1px solid #000;
}

.label {
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
}

.quantity {
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
}
